#root .noMatch {
  height: 100vh;
  width: 100vw;
  background: url("/assets/imgs/pinkpanther.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: 63%;
  background-attachment: fixed;
  position: absolute;
}

#page-not-found {
  height: 100vh;
  width: 100vw;
  margin: auto !important;
  background-color: #ffffffd0;
  text-align: center;
  padding-top: 45%;
  color: #404040;
}

.noMatch h1 {
  line-height: 18px;
}

.noMatch p {
  padding-top: 45%;
  font-weight: 500;
}




