.footer {
  padding-left: 3em;
  padding-right: 3em;
  height: 140px;
  width: 100%;
  background: rgb(204, 207, 218);
  margin-bottom: 0px;
  line-height: 0.5px;
  font-size: 11px;
}

/* .footer hr {
    margin-top: 10px; 
} */

.information {
  float: left;
}

.information ul li,
.customer-service ul li {
  padding-top: 10px;
  padding-bottom: 10px;
}

.information ul li h4,
.customer-service ul li h4 {
  margin-top: 4px;
  margin-bottom: 4px;
  font-size: 12px;
}

.customer-service {
  float: right;
}

.customer-service ul li {
  text-align: right;
}

.copy-right {
  width: 100%;
  padding: 5px;
  text-align: center;
}

.clearfix {
  content: "";
  clear: both;
  display: table;
}



